import styles from '../styles/modules/questions-user.module.scss'
import { useState, useEffect } from 'react'
import useWindowDimensions from '../hooks/useWindowDimensions'
import { useHttp } from '../hooks/http.hooks'
import Loader from './loader'
import { useHistory, useParams } from 'react-router-dom'

const initialQuestions = [
  {
    question: '',
    answers: [
      {
        answer: '',
        isTrue: false,
      },
    ],
  },
]

const QuestionsForUser = (props) => {
  const [currentIndex, setCurrentIndex] = useState(0)
  const [questions, setQuestions] = useState(initialQuestions)
  const [isSent, setIsSent] = useState(false)
  const [timeOpeningLink, setTimeOpeningLink] = useState(null)
  const { height } = useWindowDimensions()
  const { request } = useHttp()
  const history = useHistory()
  const { id } = useParams()
  const _id = id

  useEffect(
    () => {
      const getQuestions = async () => {
        try {
          const response = await request(`/api/questions/testing`, 'POST', {
            _id,
          })
          const { questions, user } = response
          if (!!user.endTime) {
            return history.push('/testing')
          }
          setQuestions(questions)
        } catch (err) {
          console.log(err)
          history.push('/404')
        }
      }
      getQuestions()
    },
    // eslint-disable-next-line
    []
  )

  useEffect(() => {
    localStorage.timeOpeningLink
      ? setTimeOpeningLink(JSON.parse(localStorage.timeOpeningLink))
      : localStorage.setItem('timeOpeningLink', JSON.stringify(Date.now()))
  }, [])

  const isPrevButtonDisabled = currentIndex === 0

  const isNextButtonDisabled =
    !questions[currentIndex].answers.find((it) => it.isTrue) ||
    currentIndex + 1 === questions.length

  const isSubmitButtonDisabled =
    !questions[currentIndex].answers.find((it) => it.isTrue) ||
    currentIndex + 1 !== questions.length

  const handleOnChange = (index) => {
    const newQuestions = [...questions]

    newQuestions[currentIndex].answers[index].isTrue = !questions[currentIndex]
      .answers[index].isTrue

    setQuestions(newQuestions)
  }

  const handleNext = () => {
    if (currentIndex + 1 !== questions.length) {
      setCurrentIndex(currentIndex + 1)
    }
  }

  const handlePrev = () => {
    if (currentIndex !== 0) {
      setCurrentIndex(currentIndex - 1)
    }
  }

  const handleSubmit = async () => {
    setIsSent(true)
    try {
      const differenceTime = Date.now() - timeOpeningLink

      await request('/api/users/add_test', 'POST', {
        _id,
        questions,
        differenceTime,
      })
      setIsSent(false)
      localStorage.clear()
      history.push('/testing')
    } catch (err) {
      console.log(err)
      setIsSent(false)
    }
  }

  return (
    <section
      className={styles.questions}
      style={{
        minHeight: height,
      }}
    >
      {isSent ? <Loader /> : null}
      <div
        className={styles.questions__wrap}
        style={{
          minHeight: height / 2,
        }}
      >
        <div className={styles.questions__title}>
          <span>{`${currentIndex + 1}/${questions.length}`}</span>
          <span>{questions[currentIndex].question}</span>
        </div>
        <ul className={styles.questions__list}>
          {questions[currentIndex].answers.map((it, index) => {
            return (
              <li className={styles.questions__item} key={index}>
                <input
                  className={styles.questions__input}
                  checked={it.isTrue}
                  type='checkbox'
                  id={`answer-${index}`}
                  onChange={() => handleOnChange(index)}
                />
                <label
                  className={styles.questions__label}
                  htmlFor={`answer-${index}`}
                >
                  {it.answer}
                </label>
              </li>
            )
          })}
        </ul>
        <div className={styles.questions__buttons}>
          <button
            disabled={isPrevButtonDisabled}
            className={styles.questions__btn}
            onClick={handlePrev}
          >
            Предыдущий вопрос
          </button>
          <button
            disabled={isNextButtonDisabled}
            className={styles.questions__btn}
            onClick={handleNext}
          >
            Следующий вопрос
          </button>
          <button
            disabled={isSubmitButtonDisabled}
            className={styles.questions__btn}
            onClick={handleSubmit}
          >
            Отправить результаты
          </button>
        </div>
      </div>
    </section>
  )
}

export default QuestionsForUser
