import styles from '../styles/modules/results.module.scss'
import { useSelector } from 'react-redux'

const Results = () => {
  const { users, questions } = useSelector((state) => state)

  const answeredСorrectlyUsers = users
    .filter((it) => {
      const tests = it.tests

      if (tests.length === 0) {
        return false
      }

      let matching = []
      for (let i = 0; i < tests.length; i++) {
        if (
          tests[i]?.answers?.map((it) => it.isTrue).join('') ===
          questions[i]?.answers?.map((it) => it.isTrue).join('')
        ) {
          matching.push(true)
        }
      }

      if (matching.length === tests.length) {
        return true
      } else {
        return false
      }
    })
    .sort((a, b) => a.endTime - b.endTime)

  const allAnswersCount = users.filter((user) => user.tests.length !== 0).length

  const usersStartedTestingCount = users.filter(
    (it) => it.isTestingStarted === true
  ).length

  const usersNotStartedTestingCount = users.length - usersStartedTestingCount

  const getCorrectAnswersCount = (question) => {
    const currentQuestion = question.question
    const userAnswers = users
      .map((it) => it.tests.filter((it) => it.question === currentQuestion))
      .flat()
      .map((it) => it.answers)
    const correctAnswer = questions
      .find((it) => it.question === currentQuestion)
      .answers.map((it) => it.isTrue)
      .join('')

    let counterCorrect = 0
    let counterIncorrect = 0

    for (const answer of userAnswers) {
      const currentUserAnswer = answer.map((it) => it.isTrue).join('')
      if (correctAnswer === currentUserAnswer) {
        counterCorrect++
      } else {
        counterIncorrect++
      }
    }

    return {
      counterCorrect,
      counterIncorrect,
    }
  }

  const tableHeader = (
    <thead>
      <tr>
        <th>Вопрос</th>
        <th>Всего ответов</th>
        <th>Правильно</th>
        <th>Не правильно</th>
        <th>Результат в процентном соотношении</th>
        <th>Первые правильно ответившие</th>
        <th>Количество пользователей, приступивших к тестированию</th>
        <th>Количество не приступивших к тестированию пользователей</th>
      </tr>
    </thead>
  )

  const CustomerRow = (question, index) => {
    const correctAnswersCount = getCorrectAnswersCount(question)

    return (
      <tr key={index}>
        <td>{question.question}</td>
        <td>{allAnswersCount}</td>
        <td>{correctAnswersCount.counterCorrect}</td>
        <td>{correctAnswersCount.counterIncorrect}</td>
        <td>
          {`${
            allAnswersCount === 0
              ? 0
              : Math.round(
                  (correctAnswersCount.counterCorrect / allAnswersCount) * 10000
                ) / 100
          }%`}
        </td>
        <td>
          {answeredСorrectlyUsers.length !== 0 &&
          index < questions.length &&
          index < answeredСorrectlyUsers.length
            ? answeredСorrectlyUsers[index].user
            : ''}
        </td>
        <td>{usersStartedTestingCount}</td>
        <td>{usersNotStartedTestingCount}</td>
      </tr>
    )
  }

  const CustomerTable = questions.map((question, index) =>
    CustomerRow(question, index)
  )

  return (
    <section className={styles.results}>
      <h2 className={styles.title}>общие результаты тестирования</h2>
      <div className={styles.table__wrap}>
        <table>
          {tableHeader}
          <tbody>{CustomerTable}</tbody>
        </table>
      </div>
    </section>
  )
}

export default Results
