import { useState } from 'react'
import Loader from './loader'
import styles from '../styles/modules/questions.module.scss'
import okIcon from '../img/ok.svg'
import { useDispatch, useSelector } from 'react-redux'
import { useHttp } from '../hooks/http.hooks'
import { setIsSent } from '../reducer/reducer'

const initPlaceholder = `Можно ли скриптом перевести посетителя на другую страницу сайта?
+Да, куда угодно.
-Да, но только в рамках текущего сайта.
-Нет, нельзя.
*
Какое событие из этого списка не существует?
+onmousescroll
-onclick
-onmouseover
-onmousemove
-onwheel
-все существуют.`

const createQuestion = (data) => {
  let question = {
    question: data[0],
    answers: [],
  }

  for (let i = 1; i < data.length; i++) {
    let isTrue

    if (data[i][0] === '+') {
      isTrue = true
    } else {
      isTrue = false
    }

    let answer = data[i].slice(1)

    question.answers.push({
      answer,
      isTrue,
    })
  }

  return question
}

const Questions = () => {
  const dispatch = useDispatch()
  const { questions, isSent } = useSelector((state) => state)
  const [newQuestions, setNewQuestions] = useState('')
  const { request } = useHttp()

  const placeholder =
    questions.length === 0
      ? initPlaceholder
      : questions.map((it) => it.question).join('\n')

  const onSubmit = async () => {
    dispatch(setIsSent(true))
    const structuredQuestions = newQuestions
      .split('*')
      .map((it) => it.split('\n'))
      .map((it) => it.filter((it) => it !== ''))
      .map((it) => createQuestion(it))
    try {
      await request(
        '/api/questions/add',
        'POST',
        { structuredQuestions },
        {
          Authorization: `Bearer ${localStorage.token}`,
        }
      )
      setNewQuestions('')
      dispatch(setIsSent(false))
    } catch (err) {
      console.log(err)
      dispatch(setIsSent(false))
    }
  }

  return (
    <section>
      {isSent ? <Loader /> : null}
      <h2 className={styles.title_questions}>список вопросов</h2>
      <section className={styles.questions}>
        <span onClick={onSubmit}>
          Загрузить на сервер
          <img src={okIcon} alt={'question'} />
        </span>
        <textarea
          placeholder={placeholder}
          rows='25'
          value={newQuestions}
          onChange={(e) => {
            setNewQuestions(String(e.target.value))
          }}
        />
      </section>
    </section>
  )
}

export default Questions
