import styles from '../styles/modules/results.module.scss'
import { useSelector, useDispatch } from 'react-redux'
import { setMenuTab, setCurrentUserId } from '../reducer/reducer'
import okIcon from '../img/ok.svg'

const UserResults = () => {
  const { users } = useSelector((state) => state)
  const dispatch = useDispatch()

  return (
    <section className={styles.results}>
      <h2 className={styles.title}>личные результаты тестирования</h2>
      <div className={styles.table__wrap}>
        <ul className={styles.list}>
          {users.map((it) => {
            return (
              <li className={styles.item} key={it._id}>
                <div className={styles.name}>{`${it.user} (${it.email})`}</div>
                <div className={styles.btn__wrap}>
                  <button className={styles.button}>
                    <span>Отправить результаты тестирования</span>
                    <img src={okIcon} alt={'question'} />
                  </button>
                  <button
                    className={styles.button}
                    onClick={() => {
                      dispatch(setMenuTab('viewAnswers'))
                      localStorage.setItem('menuTab', 'viewAnswers')
                      dispatch(setCurrentUserId(it._id))
                      localStorage.setItem('currentUserId', it._id)
                    }}
                  >
                    <span>Просмотр ответов</span>
                    <img src={okIcon} alt={'question'} />
                  </button>
                  {/* <button className={styles.button}>
                    <span>Опубликовать результаты</span>
                    <img src={okIcon} alt={'question'} />
                  </button> */}
                </div>
              </li>
            )
          })}
        </ul>
      </div>
    </section>
  )
}

export default UserResults
