const menuTab = localStorage.menuTab ? localStorage.menuTab : ''
const currentUserId = localStorage.currentUserId
  ? localStorage.currentUserId
  : ''

const initialState = {
  auth: false,
  menuTab,
  users: [],
  questions: [],
  currentUserId,
  isSent: false,
}

const ActionType = {
  SET_AUTH: 'SET_AUTH',
  SET_MENU_TAB: 'SET_MENU_TAB',
  SET_USERS: 'SET_USERS',
  SET_QUESTIONS: 'SET_QUESTIONS',
  SET_CURRENT_USER_ID: 'SET_CURRENT_USER_ID',
  SET_IS_SENT: 'SET_IS_SENT',
}

export const setAuth = (status) => {
  return {
    type: ActionType.SET_AUTH,
    payload: status,
  }
}

export const setMenuTab = (status) => {
  return {
    type: ActionType.SET_MENU_TAB,
    payload: status,
  }
}

export const setUsers = (status) => {
  return {
    type: ActionType.SET_USERS,
    payload: status,
  }
}

export const setQuestions = (status) => {
  return {
    type: ActionType.SET_QUESTIONS,
    payload: status,
  }
}

export const setCurrentUserId = (status) => {
  return {
    type: ActionType.SET_CURRENT_USER_ID,
    payload: status,
  }
}

export const setIsSent = (status) => {
  return {
    type: ActionType.SET_IS_SENT,
    payload: status,
  }
}

export const reducer = (state = initialState, action) => {
  switch (action.type) {
    case ActionType.SET_AUTH: {
      return { ...state, auth: action.payload }
    }

    case ActionType.SET_MENU_TAB: {
      return { ...state, menuTab: action.payload }
    }

    case ActionType.SET_USERS: {
      return { ...state, users: action.payload }
    }

    case ActionType.SET_QUESTIONS: {
      return { ...state, questions: action.payload }
    }

    case ActionType.SET_CURRENT_USER_ID: {
      return { ...state, currentUserId: action.payload }
    }

    case ActionType.SET_IS_SENT: {
      return { ...state, isSent: action.payload }
    }

    default:
      return state
  }
}
