import styles from '../styles/modules/main.module.scss'
import { useSelector, useDispatch } from 'react-redux'
import { setUsers, setQuestions } from '../reducer/reducer'
import { useHttp } from '../hooks/http.hooks'
import { useEffect } from 'react'
import Users from './users'
import Questions from './questions'
import Results from './results'
import UserResults from './user-results'
import ViewAnswersUser from './view-answers-user'

const getComponentByTab = (tab) => {
  switch (tab) {
    case `users`:
      return <Users />
    case `questions`:
      return <Questions />
    case `userResults`:
      return <UserResults />
    case `results`:
      return <Results />
    case `viewAnswers`:
      return <ViewAnswersUser />
    default:
      return null
  }
}

const Main = () => {
  const { menuTab, isSent } = useSelector((state) => state)
  const dispatch = useDispatch()
  const { request } = useHttp()

  useEffect(() => {
    const getUsers = async () => {
      try {
        const response = await request(`/api/users/all`, 'GET', null, {
          Authorization: `Bearer ${localStorage.token}`,
        })
        dispatch(setUsers(response))
      } catch (err) {
        console.log(err)
      }
    }
    getUsers()
  }, [dispatch, request, isSent])

  useEffect(() => {
    const getQuestions = async () => {
      try {
        const response = await request(`/api/questions/active`, 'GET', null, {
          Authorization: `Bearer ${localStorage.token}`,
        })
        const questions = response.length === 0 ? [] : response[0].structuredQuestions
        dispatch(setQuestions(questions))
      } catch (err) {
        console.log(err)
      }
    }

    getQuestions()
  }, [dispatch, request, isSent])

  return (
    <main className={styles.main}>
      <div className={styles.main__wrap}>{getComponentByTab(menuTab)}</div>
    </main>
  )
}

export default Main
