import { BrowserRouter, Switch, Route, Redirect } from 'react-router-dom'
import Admin from './components/admin'
import QuestionsForUser from './components/questions-for-user'
import EndTesting from './components/end-testing'
import NotFound from './components/404'

const App = () => {

  return (
    <BrowserRouter>
      <Switch>
        <Route exact path='/admin'>
          <Admin />
        </Route>
        <Route exact path='/testing'>
          <EndTesting />
        </Route>
        <Route exact path='/testing/:id'>
          <QuestionsForUser />
        </Route>
        <Route exact path='/404'>
          <NotFound />
        </Route>
        <Redirect to='/404' />
      </Switch>
    </BrowserRouter>
  )
}

export default App
