import { useState } from 'react'
import { useHttp } from '../hooks/http.hooks'
import styles from '../styles/modules/login.module.scss'
import Loader from './loader'
import { useDispatch } from 'react-redux'
import { setAuth } from '../reducer/reducer'

const initialData = {
  email: `apkpro@apkpro.ru`,
  password: `123`,
}

const LogIn = () => {
  const [logInData, setLogInData] = useState(initialData)
  const [isSent, setIsSent] = useState(false)
  const { request } = useHttp()
  const dispatch = useDispatch()

  const handleSubmit = async (evt) => {
    evt.preventDefault()
    setIsSent(true)

    try {
      // путь для регистрации пользователя с правами админа
      // await request('/api/auth/register', 'POST', logInData)

      const response = await request('/api/auth/login', 'POST', logInData)
      localStorage.setItem('token', response.token)    
      setIsSent(false)
      dispatch(setAuth(true))
    } catch (err) {
      console.log(err)
      setIsSent(false)
      dispatch(setAuth(false))    
    }
  }

  return (
    <section className={styles.login}>
      {isSent ? <Loader /> : null}
      <div className={styles.login__wrap}>
        <h2>Вход</h2>
        <form onSubmit={handleSubmit}>
          <div className={styles.login__form_wrap}>
            <label>
              Логин
              <input
                type='email'
                placeholder={logInData.email}
                required
                value={logInData.email}
                onChange={(evt) =>
                  setLogInData({
                    ...logInData,
                    email: String(evt.target.value).trim(),
                  })
                }
              />
            </label>
            <label>
              Пароль
              <input
                type='password'
                placeholder={logInData.password}
                required
                value={logInData.password}
                onChange={(evt) =>
                  setLogInData({
                    ...logInData,
                    password: String(evt.target.value).trim(),
                  })
                }
              />
            </label>
            <button className={styles.login__button} disabled={isSent}>
              Войти
            </button>
          </div>
        </form>
      </div>
    </section>
  )
}

export default LogIn
