import styles from '../styles/modules/menu.module.scss'
import { useDispatch, useSelector } from 'react-redux'
import { setMenuTab } from '../reducer/reducer'
import user from '../img/user.svg'
import question from '../img/question.svg'
import book from '../img/book.svg'
import list from '../img/list.svg'

const buttons = [
  {
    name: 'Список вопросов',
    tab: 'questions',
    imgSrc: question,
  },
  {
    name: 'Список участников',
    tab: 'users',
    imgSrc: book,
  },
  {
    name: 'общие результаты',
    tab: 'results',
    imgSrc: list,
  },
  {
    name: 'результаты пользователей',
    tab: 'userResults',
    imgSrc: user,
  },
]

const Menu = () => {
  const dispatch = useDispatch()
  const { menuTab } = useSelector((state) => state)

  const handleTabClick = (tab) => {
    if (menuTab === tab) {
      dispatch(setMenuTab(''))
      localStorage.setItem('menuTab', '')
    } else {
      dispatch(setMenuTab(tab))
      localStorage.setItem('menuTab', tab)
    }
  }

  return (
    <section className={styles.header}>
      <div className={styles.header__wrap}>
        <h1 className={styles.header__title}>Система диагностик</h1>
        <ul className={styles.header__list}>
          {buttons.map((it, index) => {
            return (
              <li className={styles.header__item} key={index}>
                <button
                  className={styles.header__button}
                  onClick={() => {
                    handleTabClick(it.tab)
                  }}
                >
                  <img src={it.imgSrc} alt={it.name} />
                  <span>{it.name}</span>
                </button>
              </li>
            )
          })}
        </ul>
      </div>
    </section>
  )
}

export default Menu
