import { useState, useEffect } from 'react'
import styles from '../styles/modules/users.module.scss'
import okIcon from '../img/ok.svg'
import { useHttp } from '../hooks/http.hooks'
import Loader from './loader'

const initialUser = {
  user: '',
  email: '',
}

const Users = () => {
  const [users, setUsers] = useState([])
  const [newUser, setNewUser] = useState(initialUser)
  const [textarea, setTextarea] = useState('')
  const [isSent, setIsSent] = useState(false)
  const [currentId, setCurrentId] = useState(null)
  const { request } = useHttp()

  useEffect(() => {
    const getUsers = async () => {
      try {
        const response = await request(`/api/users/all`, 'GET', null, {
          Authorization: `Bearer ${localStorage.token}`,
        })
        setUsers(response)
      } catch (err) {
        console.log(err)
      }
    }
    getUsers()
  }, [isSent, request])

  useEffect(() => {
    const onKeypress = async (evt) => {
      if (evt.key === 'Enter' && newUser.user && newUser.email) {
        handleNewUser()
      }

      if (evt.key === 'Enter' && !newUser.user && !newUser.email) {
        setIsSent(true)
        try {
          const editUser = users.find((it) => it._id === currentId)
          await request('/api/users/edit', 'POST', editUser, {
            Authorization: `Bearer ${localStorage.token}`,
          })
          setIsSent(false)
          setCurrentId(null)
        } catch (err) {
          console.log(err)
          setIsSent(false)
          setCurrentId(null)
        }
      }
    }

    document.addEventListener('keypress', onKeypress)

    return () => {
      document.removeEventListener('keypress', onKeypress)
    }
  })

  const onSendLinkButtonClick = async (email, user, id) => {
    setIsSent(true)
    try {
      await request(
        '/api/mail',
        'POST',
        { email, user, id },
        {
          Authorization: `Bearer ${localStorage.token}`,
        }
      )
      setIsSent(false)
    } catch (err) {
      console.log(err)
      setIsSent(false)
    }
  }

  const handleNewUser = async () => {
    setIsSent(true)
    try {
      await request(
        '/api/users/add',
        'POST',
        { newUser },
        {
          Authorization: `Bearer ${localStorage.token}`,
        }
      )
      setIsSent(false)
      setNewUser(initialUser)
    } catch (err) {
      console.log(err)
      setIsSent(false)
      setNewUser(initialUser)
    }
  }

  const handleEditUser = async (editUser) => {
    setIsSent(true)
    try {
      await request('/api/users/edit', 'POST', editUser, {
        Authorization: `Bearer ${localStorage.token}`,
      })
      setIsSent(false)
    } catch (err) {
      console.log(err)
      setIsSent(false)
    }
  }

  const handleUploadUsers = async () => {
    if (!textarea) {
      return
    }
    setIsSent(true)
    const newUserArray = textarea.trim().split('\n')
    const newUsers = newUserArray
      .map((it) => it.split(' '))
      .map((it) => {
        let user = ''
        let email = ''

        for (let i = 0; i < it.length; i++) {
          if (i === 0) {
            user = it[i]
          }
          if (i !== 0 && i !== it.length - 1) {
            user = user + ` ${it[i]}`
          }
          if (i === it.length - 1) {
            email = it[i]
          }
        }

        return {
          user,
          email,
        }
      })

    try {
      await request('/api/users/add-all', 'POST', newUsers, {
        Authorization: `Bearer ${localStorage.token}`,
      })
      setTextarea('')
      setIsSent(false)
    } catch (err) {
      console.log(err)
      setIsSent(false)
    }
  }

  return (
    <section className={styles.list__wrap}>
      {isSent ? <Loader /> : null}
      <h2 className={styles.title}>список участников</h2>
      <ul className={styles.list}>
        {users.map((user, index) => {
          return (
            <li className={styles.item} key={index}>
              <input
                className={styles.user}
                type='text'
                value={user.user}
                onChange={(e) => {
                  setCurrentId(user._id)
                  const newUsersArray = [...users]
                  const currentUser = newUsersArray[index]
                  const editUser = {
                    ...currentUser,
                    user: String(e.target.value),
                  }
                  newUsersArray.splice(index, 1, editUser)
                  setUsers(newUsersArray)
                }}
                onBlur={() => {
                  handleEditUser(users[index])
                  setCurrentId(null)
                }}
              />
              <input
                className={styles.mail}
                type='email'
                value={user.email}
                onChange={(e) => {
                  setCurrentId(user._id)
                  const newUsersArray = [...users]
                  const currentUser = newUsersArray[index]
                  const editUser = {
                    ...currentUser,
                    email: String(e.target.value),
                  }
                  newUsersArray.splice(index, 1, editUser)
                  setUsers(newUsersArray)
                }}
                onBlur={() => {
                  handleEditUser(users[index])
                  setCurrentId(null)
                }}
              />
              <button
                className={styles.button}
                onClick={() => {
                  onSendLinkButtonClick(user.email, user.user, user._id)
                }}
              >
                <span> Отправить ссылку на тестирование</span>
                <img src={okIcon} alt={'question'} />
              </button>
            </li>
          )
        })}
        <li className={styles.item__new}>
          <label className={styles.label__user}>
            Добавить Ф.И.О. участника
            <input
              className={styles.new_user}
              autoFocus
              type='text'
              value={newUser.user}
              onChange={(e) => {
                setNewUser({
                  ...newUser,
                  user: String(e.target.value),
                })
              }}
              onBlur={() => {
                if (newUser.user && newUser.email) {
                  handleNewUser()
                }
              }}
            />
          </label>
          <label className={styles.label__email}>
            E-mail
            <input
              className={styles.new_user}
              type='email'
              value={newUser.email}
              onChange={(e) => {
                setNewUser({
                  ...newUser,
                  email: String(e.target.value).trim(),
                })
              }}
              onBlur={() => {
                if (newUser.user && newUser.email) {
                  handleNewUser()
                }
              }}
            />
          </label>
        </li>

        <li className={styles.item}>
          <label className={styles.label__textarea_wrap}>
            Загрузить участников списком
            <textarea
              placeholder='Данные о пользователях построчно, новый пользователь с новой строки, последней записью в строке должен быть адрес электронной почты. Загрузка пользователей в этом поле полностью обновит данные на сервере, предыдущие пользователи и их  результаты будут удалены'
              rows='15'
              value={textarea}
              onChange={(e) => {
                setTextarea(e.target.value)
              }}
            />
          </label>
          <button
            className={styles.button}
            disabled={!textarea}
            onClick={handleUploadUsers}
          >
            <span>Загрузить участников на сервер</span>
            <img src={okIcon} alt={'question'} />
          </button>
        </li>
      </ul>
    </section>
  )
}

export default Users
